import React from "react";
import { Button, Space, Result } from "antd";
import { useNavigate } from "react-router-dom";
import NotFoundImage from "./notfound-image"
import { toRelativeUrl } from "components/utils/utils";
export default function NotFoundPage() {
  const navigate = useNavigate();
  return (
    <Result
      title="Page not found"
      subTitle="The page you looking for can't be found."
      icon={<NotFoundImage />}
      extra={
        <Space>
          <Button type="primary" onClick={() => navigate(toRelativeUrl("/admin"))}>
            Go Home
          </Button>
          <Button type="primary" onClick={() => navigate(-1)}>
            Go Back
          </Button>
        </Space>
      }
    />
  );
}
